<template>
  <div id="gift">
    <common-header :title="'礼物包'" link="礼物记录" @linkClick="linkTo('GiftData')"></common-header>
    <div class="gift__tab">
      <span data-id='1' class="tab" :class="{'active': active == 1}" @click="changeType(1)">实物礼物</span>
      <span data-id='2' class="tab" :class="{'active': active == 2}" @click="changeType(2)">定制礼物</span>
      <span data-id='3' class="tab" :class="{'active': active == 3}" @click="changeType(3)">虚拟礼物</span>
    </div>

    <Gift v-if="active == 1" :index='index'/>
    <Gift type='DIY' v-else-if="active == 2" :index='index'/>
    <Gift @checked="getCheckedIndex" type='virtual' v-else-if="active == 3" :index='index'/>

    <div class="gift__bottom" v-show="active == 3">
      <div class="count">
        <span class="count__text">数量</span>
        <Counter :num="value" :max="5" @change="valueChange"/>
      </div>
      <div class="submit">兑换零钱</div>
    </div>
  </div>  
</template>

<script>
  import Gift from '@/components/Gift/Gift';
  import Counter from '@/components/Counter/Counter';

  export default {
    data() {
      return {
        active: '1',
        value: 1,
        index:1,
      }
    },

    methods: {
      changeType(type) {
        this.active = type;
        this.index = type;
      },

      //计算器
     valueChange(val) {
        this.value += val;
     	},

      //当type = 3， 点击选中返回选中下标
      getCheckedIndex(index) {
        console.log(index)
      }
    },

    components: {
      Gift,
      Counter
    }
  }
</script>


<style lang="scss">
  #gift {
    height: 100%;
    background: #f0f0f0;

    .gift__tab {
      height: 1.067rem;
      line-height: 1.067rem;
      font-size: 0;
      background: #fff;
      border-bottom: 1px solid #eee;

      .tab {
        display: inline-block;
        width: 33.3%;
        text-align: center;
        font-size: 0.4rem;
        color: #333;
      }

      .tab.active {
        position: relative;
        color: #de7fef;

        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          height: 2px;
          width: 100%;
          background: #de7fef;
        }
      }
    }

    .gift__bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: space-between;
      height: 1.3067rem;
      line-height: 1.3067rem;
      width: 100%;
      font-size: 0.453rem;
      background: #fff;
      border-top: 1px solid #eee;

      .count {
        display: flex;

        .count__text {
          padding: 0 0.6rem 0 0.4rem;
        }
      }

      .submit {
        padding: 0 1.07rem;
        color: #fff;
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }
  }
</style>




