<template>
  <div id="counter">
    <div class="controller">
      <span class="control-sub control"
            :class="{'c-ccc': num == 0}"
            @click="count(-1)"
      >-</span>

      <input class="control-input" type="num" name="" :value="num" disabled>

      <span class="control-add control"
            :class="{'c-ccc': max?num >= max: false}"
            @click="count(1)"
      >+</span>
    </div>
  </div>  
</template>

<script>
  export default {
    props: {

      //input传进来的value值
      num: {
        type: Number
      },

      //input的最大值
      max: {
        type: Number
      },
    },

    methods: {

      count(type) {
        if(type == -1 && this.num <= 0) {
          console.log('不能少于0')
          return false;
        }
        if(this.max && this.num >= this.max && type == 1) {
          console.log('不能大于最大值')
          return false;
        }
        this.$emit('change', type);
      }
    }
  }
</script>

<style lang="scss">
  #counter {
    .controller {
      margin-top: 0.186rem;
      color: #333;
      font-size: 0;

      .control {
        display: inline-block;
        width: 0.8rem;
        height: 0.8rem;
        line-height: 0.8rem;
        text-align: center;
        font-size: 0.67rem;
        border: 1px solid rgb(204, 204, 204);
        box-sizing: content-box;
      }
      .c-ccc{
        color: #ccc;
      }
      .control-sub {
        border-right: 0;
      }
      .control-add {
        border-left: 0;
      }
      .control-input {
        font-size: 0.53rem;
        width: 1.6rem;
        height: 0.8rem;
        line-height: 0.8rem;
        vertical-align: top;
        text-align: center;
        border: 1px solid rgb(204, 204, 204);
        box-sizing: content-box;
      }
    }
  }
  
</style>



