<template>
  <div id="gift__list">
    <div class="gift" v-for="item in 4" @click="linkToUrl('giftDetail?type=' + type)" v-if="type != 'virtual'">
      <div class="gift__left">
        <img src="../../assets/pic.png" alt="">
      </div>
      <div class="gift__right">
        <div class="gift__right__name">酷美人 夜光石手链 男女款萤石手链 手串手环饰品...</div>
        
        <div class="gift__right__bottom">
          <div class="bottom__price" v-if="type != 'virtual'">￥180</div>
          <div class="bottom__info">
            <span class="user"  v-if="type != 'virtual'">赠送者：李四</span>
            <span class="heart" v-if="type == 'virtual'">
              <span class="heart__icon"></span>
              <span class="heart__num">2</span>
            </span>
            <span class="num">x1</span>
          </div>
        </div>
      </div>
    </div>

    <div class="gift virtual" v-for="(item,index) in goodses" :key="index" v-if="type == 'virtual'">
      <div class="gift__check">
        <input class="radio" type="radio" name="address"  @click="checked(index)"/>
      </div>
      <div class="gift__left">
        <img src="../../assets/pic.png" alt="">
      </div>
      <div class="gift__right">
        <div class="gift__right__name">{{item.name}}</div>
        
        <div class="gift__right__bottom">
          <div class="bottom__price" v-if="type != 'virtual'">￥{{item.price}}</div>
          <div class="bottom__info">
            <span class="user"  v-if="type != 'virtual'">赠送者：{{item.senderName}}</span>
            <span class="heart" v-if="type == 'virtual'">
              <span class="heart__icon"></span>
              <span class="heart__num">{{item.price}}</span>
            </span>
            <span class="num">x{{item.num}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>  
</template>

<script>
  export default {
    props: {
      type: {
        type: String,
        default: 'normal'
      },
      index:{
      	type:Number,
      	default:1
      }
    },
    
    data() {
      return {
        data: [{},{},{}],
        activeIndex: '',
        giftType:1,
        goodses:[],
      }
    },
    watch:{
    	index(){
    		console.log(this.index)
    		this.getGiftGoodsList()
    	}
    },
		mounted(){
			this.getGiftGoodsList()
		},
    methods: {
      //选中并返回选中下标
      checked(index) {
        this.$emit('checked', index);
      },
      getGiftGoodsList(){
      	let giftType =this.index
      	this.$Api.getGiftGoodsList(giftType).then((res)=>{
      		console.log(res)
      		if(res.q.s == 0){
      			
      		}
      	})
      },
    }
  }
</script>


<style lang="scss">
  #gift__list {
    .gift {
      padding: 0.267rem 0.4rem;
      height: 2.9333rem;
      background: #fff;
      box-sizing: content-box;
      border-bottom: 1px solid #eee;

      .gift__left {
        float: left;

        img {
          width: 2.9333rem;
          height: 2.9333rem;
        }
      }

      .gift__right {
        position: relative;
        float: right;
        width: 5.867rem;
        height: 2.9333rem;

        .gift__right__name {
          font-size: 0.3733rem;
        }

        .gift__right__bottom {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;

          .bottom__price {
            font-size: 0.453rem;
            font-weight: 600;
            color: #fe4436;
          }

          .bottom__info {
            position: relative;
            width: 100%;
            color: #666;

            span {
              display: inline-block;
            }

            .num {
              position: absolute;
              right: 0;
              bottom: 0;
              vertical-align: middle;
            }

            .heart {
              vertical-align: middle;
              .heart__icon {
                display: inline-block;
                width: 0.5067rem;
                height: 0.45333rem;
                background: url('../../assets/heart.png') no-repeat;
                background-size: 100%;
                background-position: center;
                vertical-align: middle;
              }

              .heart__num {
                color: #de7fef;
                vertical-align: middle;
              }
            }
          }
        }
      }
    }

    .virtual {
      .gift__right {
        width: 5.267rem;
      }

      .gift__check {
        float: left;
        display: flex;
        height: 100%;
        align-items: center;

        .radio{
          position: relative;
          -moz-appearance: none;
          -webkit-appearance: none;
          float: left;
          margin-right: 0.2rem;
          width: 0.533rem;
          height: 0.533rem;

          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 0.533rem;
            height: 0.533rem;
            background: #fff;
            border: 1px solid #eee;
            border-radius: 50%;
            z-index: 9;
            box-sizing: border-box;
          }

          &:checked {
            &::after {
              border: none;
              background: url("../../assets/cart/ico_checked.png") no-repeat center;
              background-size: 100%;
            }
          }
        }

      }
    }
  }
</style>




